<template>
    <div class="table">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item><i class="el-icon-lx-cascades"></i> 标签分类</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-button type="primary" icon="delete" class="handle-del mr10" @click="delAll">批量删除</el-button>
                <el-button type="primary" icon="delete" class="handle-del mr10" @click="newAdd">新增</el-button>
                <!-- <el-select v-model="select_cate" placeholder="筛选省份" class="handle-select mr10">
                    <el-option key="1" label="广东省" value="广东省"></el-option>
                    <el-option key="2" label="湖南省" value="湖南省"></el-option>
                </el-select> -->
                <!-- <el-input v-model="select_word" placeholder="筛选关键词" class="handle-input mr10"></el-input>
                <el-button type="primary" icon="search" @click="search">搜索</el-button> -->
            </div>
            <el-table :data="allTagsData" border class="table" ref="multipleTable" @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="55" align="center"></el-table-column>
                <el-table-column prop="tag" label="标签名字" sortable>
                </el-table-column>
                <el-table-column prop="ctime" label="创建时间">
                </el-table-column>
                <el-table-column prop="uptime" label="更新时间">
                </el-table-column>
                <el-table-column label="操作" width="180" align="center">
                    <template slot-scope="scope">
                        <el-button type="text" icon="el-icon-edit" @click="editorTag(scope.$index, scope.row)">编辑</el-button>
                        <el-button type="text" icon="el-icon-delete" class="red" @click="detailTags(scope.$index, scope.row)">删除</el-button> 
                    </template>
                </el-table-column>
            </el-table>
            


        </div>

        <!-- 编辑弹出框 -->
        <el-dialog :title="visibleTitle" :visible.sync="editVisible" width="450px">
            <el-form ref="form" :model="form" label-width="80px"  >
                <el-form-item label="标签名字" >
                    <el-input v-model="form.tag"></el-input>
                </el-form-item>
                <el-form-item label="创建时间" >
                    <el-date-picker type="date" :disabled="visibleTitle=='编辑标签'?true:false" placeholder="选择日期" v-model="form.ctime" value-format="yyyy-MM-dd" style="width: 100%;"></el-date-picker>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false">取 消</el-button>
                <el-button type="primary" @click="saveEdit">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 删除提示框 -->
        <el-dialog title="提示" :visible.sync="delVisible" width="300px" center>
            <div class="del-dialog-cnt">删除不可恢复，是否确定删除？</div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="delVisible = false">取 消</el-button>
                <el-button type="primary" @click="deleteRow">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import axios from '../../axios'
    export default {
        name: 'basetable',
        data() {
            return {
                is_search: false,
                editVisible: false,
                delVisible: false,
                form: {
                    tags: '',
                    ctime: '',
                },
                multipleSelection:[],

                visibleTitle:'',
                allTagsData:[],
                tagId:'',



            }
        },
        components:{
            
        },
        created() {
            this.getAllTags();
        },
        computed: {
            data() {
                return this.tableData.filter((d) => {
                    let is_del = false;
                    for (let i = 0; i < this.del_list.length; i++) {
                        if (d.name === this.del_list[i].name) {
                            is_del = true;
                            break;
                        }
                    }
                    if (!is_del) {
                        if (d.address.indexOf(this.select_cate) > -1 &&
                            (d.name.indexOf(this.select_word) > -1 ||
                                d.address.indexOf(this.select_word) > -1)
                        ) {
                            return d;
                        }
                    }
                })
            }
        },
        methods: {
           
            // 获取所有标签
            getAllTags(){
                axios.get('/api/web/queryAllTags')
                    .then(res=>{
                        console.log(res)
                        this.allTagsData=res.data;
                    }).catch(err=>{
                        console.log(err)
                    })
            },
            
        //    删除标签
            detailTags(index, row) {
                console.log(row._id)
                axios.post('/api/web/dellTags',{
                    id:row._id
                }).then(res=>{
                    if(res.status==200){
                        this.$message({
                            showClose: true,
                            message: '恭喜你，删除成功！',
                            type: 'success'
                        });
                        this.getAllTags();
                    }
                    console.log(res);
                })
            },
            //新增标签
            newAdd(){
                this.nullBlog();
                this.visibleTitle='新增标签';
                this.editVisible = true;
                // this.insertBlog();
            },
            // 清除form内容
            nullBlog(){
                this.form={
                    tag: '',
                    ctime: '',
                }
            },

            
            submit(){
                console.log(this.content);
                this.$message.success('提交成功！');
            },

            // 保存编辑
            saveEdit() {
                // this.$set(this.tableData, this.idx, this.form);
                // alert(this.form)
                if(this.visibleTitle=='编辑标签'){
                    let subArr = {
                        id:this.tagId,
                        tag:this.form.tag,
                        ctime:this.form.ctime || new Date(),
                        uptime: new Date().toLocaleString("chinese", { hour12: false })
                    }
                    console.log('主体信息',subArr);
                    axios.post('/api/web/updateOneTags',subArr).then(res=>{
                        if(res.status==200){
                            this.$message({
                                showClose: true,
                                message: '恭喜你，更新成功！',
                                type: 'success'
                            });
                            this.editVisible = false;
                            this.tagId='';
                            this.nullBlog();
                            this.getAllTags();
                        }
                        console.log('添加返回信息：',res);
                    })

                }else if(this.visibleTitle=='新增标签'){
                    let subArr = {
                        tag:this.form.tag,
                        ctime:this.form.ctime || new Date(),
                        uptime:this.form.uptime || new Date()
                    }
                    this.addTags(subArr);
                    console.log('提交信息：',this.form)
                }
                
            },
            addTags(submitArr){
                axios.post('/api/web/insertTags',submitArr).then(res=>{
                    if(res.status==200){
                        this.$message({
                            showClose: true,
                            message: '恭喜你，添加成功!',
                            type: 'success'
                        });
                        this.editVisible = false;
                        this.nullBlog();
                        this.getAllTags();
                    }
                    console.log('添加返回信息：',res);
                })
                        
            },

            // 编辑标签
            editorTag(index,row){
                this.visibleTitle='编辑标签';
                this.editVisible = true;
                console.log('这是id:',row._id)
                this.tagId = row._id;
                this.form={
                            tag:row.tag,
                            ctime:row.ctime,
                        }
            },

            // 批量删除标签
            delAll() {
                let str = [];
                if(this.multipleSelection.length==0){
                    this.$message({
                            showClose: true,
                            message: '请选中要删除的标签',
                            type: 'warning'
                        });
                    return false;
                }
                this.multipleSelection.forEach((item,index) => {
                    str.push(item._id)
                });
                console.log(str)
                axios.post('/api/web/dellBatchTags',{
                    idArr:str
                }).then(res=>{
                    if(res.status==200){
                        this.$message({
                            showClose: true,
                            message: '恭喜你，批量删除成功！',
                            type: 'success'
                        });
                        this.getAllTags();
                    }
                    console.log(res);
                })
                // this.multipleSelection = [];
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
                console.log(this.multipleSelection);
            },
            




           
            
            
            
            // 确定删除
            deleteRow(){
                this.tableData.splice(this.idx, 1);
                this.$message.success('删除成功');
                this.delVisible = false;
            }
        }
    }

</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .del-dialog-cnt{
        font-size: 16px;
        text-align: center
    }
    .table{
        width: 100%;
        font-size: 14px;
    }
    .red{
        color: #ff0000;
    }
    .mr10{
        margin-right: 10px;
    }
</style>
