// 添加请求拦截器
axios.interceptors.request.use(function (config) {
    // 在发送请求之前做些什么
    // console.log(config)
    // console.log(localStorage.getItem('yddl_token'));
    if(localStorage.getItem('BXWToken')){
      config.headers.common['Token']=localStorage.getItem('BXWToken')
      console.log('hhh',localStorage.BXWToken,localStorage.getItem('BXWToken'))
      config.headers.Authorization = localStorage.BXWToken;
      }
    config.params = {
        ...config.params,
        // appkey:"bxw123_1554272180284"
    }
    return config;
  }, function (error) {
    // 对请求错误做些什么
    return Promise.reject(error);
  });
axios.interceptors.response.use( (response)=> {
    // 对响应数据做点什么
    console.log('+++++++------------',response);
    console.log(response.data.status);
    switch (response.data.status) {
      case '1001':
      console.log(response.data.status);
      router.push({
                name:"login",
                // querry:{redirect:router.currentRoute.fullPath}//从哪个页面跳转
              })
      Message.error({
        message: '登录状态失效，请重新登录',
      })
      // this.$store.commit("setUserName",'')
      // this.$store.commit("changeLogin",false)
      // this.$store.commit('del_token')
      break
    }
    return response;
  }, function (error) {
    // if(error=='Error: Request failed with status code 401'){
    //   console.log('错误：','ok')
    // }else{
      
    // }
    return Promise.reject(error);
    
    // 对响应错误做点什么
    
  });
